<template>
    <div class="distributorMain" v-loading="loading">
        <ul class="top" v-if="!loading">
            <li class="li" v-for="item in topList">
                <div class="li-box"  @click="gotoRouter(item)">
                    <i :class="item.icon"></i>
                    <div class="text-box">
                        <p class="text">{{item.title}}</p>
                        <p class="price">{{item.value}}</p>
                    </div>
                </div>
            </li>
        </ul>
        <div class="menu" v-if="!loading">
            <div class="menu-item" v-for="item in menuList" @click="gotoRouter(item)">
                <span class="bbx-font mainFontColor" :class="item.icon"></span>
                <span class="text">{{ item.text }}</span>
                <span class="jiantou bbx-font bbx-icon-iconfontjiantou2"></span>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "distributorMain",
        data(){
            return {
                loading:true,
                baseInfo:{},
                resultType:1,
                tableData:{}
            }
        },
        computed: {
            topList:{
                get() {
                    let re = [];
                    if (this.baseInfo.hasOwnProperty('accountInfo')) {
                        if(this.baseInfo.accountInfo.hasOwnProperty('distributionTotal')){
                            re.push({
                                icon: 'bbx-font bbx-icon-weibiaoti--5',
                                title: this.$L('订单总金额'),
                                value: this.baseInfo.accountInfo.distributionTotal,
                                router: 'distributorOrderMobile',
                                params: {
                                    type: "all"
                                },
                            });
                        }
                        if(this.baseInfo.accountInfo.hasOwnProperty('distributionCommission')){
                            re.push({
                                icon: 'bbx-font bbx-icon-weibiaoti--6',
                                title: this.$L('分销总佣金'),
                                value: this.baseInfo.accountInfo.distributionCommission,
                                router: 'distributorWithdrawalMobile'
                            });
                        }
                        if(this.baseInfo.accountInfo.hasOwnProperty('distributionWithdraw')){
                            re.push({
                                icon: 'bbx-font bbx-icon-weibiaoti--4',
                                title: this.$L('可提现佣金'),
                                value: this.baseInfo.accountInfo.distributionWithdraw,
                                router: 'applyWithdrawalMobile'
                            });
                        }
                        re.push({
                            icon: 'bbx-font bbx-icon-dianputuiguang',
                            title: this.$L('店铺推广'),
                            value: this.$L(' 推广设置'),
                            router: 'promotionSettingsMobile'
                        });
                    }
                    return re;
                }
            },
			menuList:{
				get(){
					let list = [
						{
						    icon:'bbx-icon-distributor',
						    text:this.$L('我的分销商'),
						    router:'distributorListMobile',
						    params:{
						        gid:1,
						        type:'all'
						    }
						},
						{
						    icon:'bbx-icon-tuandui',
						    text:this.$L('我的会员'),
						    router:'distributorMemberMobile',
						    params:{
						        gid:4,
						        type:'all'
						    }
						},
						{
						    icon:'bbx-icon-fenxiaodingdan',
						    text:this.$L('分销订单'),
						    router:'distributorOrderMobile',
						    params:{
						        type:'all',
						    }
						},
						{
						    icon:'bbx-icon-shengqian',
						    text:this.$L('我的佣金'),
						    router:'distributorWithdrawalMobile'
						},
						{
						    icon:'bbx-icon-shezhi1',
						    text:this.$L('分销设置'),
						    router:'distributorSettingsMobile'
						}
					];
					if(this.baseInfo.sales === 1){
						list.push({
							icon:'bbx-icon-dc',
							text:this.$L('分销排行'),
							router:'distributorRankingMobile',
							params:{
								type:'sales',
							}
						});
					}else if(this.baseInfo.commission === 1){
						list.push({
							icon:'bbx-icon-dc',
							text:this.$L('分销排行'),
							router:'distributorRankingMobile',
							params:{
								type:'commission',
							}
						});
					}else if(this.baseInfo.offline === 1){
						list.push({
							icon:'bbx-icon-dc',
							text:this.$L('分销排行'),
							router:'distributorRankingMobile',
							params:{
								type:'offline',
							}
						});
					}
					//http://localhost:8081/uc#/distributor/modify
                    list.push({
                        icon:'bbx-icon-liuyanban',
                        text:this.$L('页面信息修改'),
                        router:'distributorModifyMobile'
                    });
					list.push({
                        icon:'bbx-icon-ketixian',
                        text:this.$L('申请提现'),
                        router:'applyWithdrawalMobile'
                    });
					return list;
				}
			}
        },
        created(){
            // // 先进行审核页面的判断
            this.loading = true;
            if(this.$isMobile()){
                this.review();
                this.getData();
            }else{
                this.$router.push({name:'distributorList'});
            }
        },
        methods:{
            gotoRouter(item) {
                if (item && item.hasOwnProperty('router')) {
                    let params = {};
                    if (item.hasOwnProperty('params')) {
                        Object.assign(params, item.params);
                    }
                    if (item.hasOwnProperty('paramsData')) {
                        Object.assign(params, item.paramsData);
                    }
                    this.$router.push({name: item.router, params: params});
                } else if (item && typeof item.func === "function") {
                    if (item.hasOwnProperty('funcData')) {
                        item.func(item.funcData);
                    } else {
                        item.func();
                    }
                }
            },
            getData() {
                const _self = this;
                _self.loading = true;
                _self.$api('indexBaseInfo', {}, function (reData) {
                    if (reData.flag === 1) {
                        _self.baseInfo = reData.data;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });
            },
            review() {
                const _self = this;
                _self.loading = true;
                _self.$api('reviewResult', {}, function (reData) {
                    if (reData.flag === 1) {
                        _self.resultType = reData.data.resultType;
                        _self.tableData = reData.data;
                        if (_self.resultType !== 1) {
                            _self.$router.push({name:'distributorAudit',query:{data:_self.tableData}});
                        }
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });

            },
            //点击
            gotoRouter(item) {
                if (item && item.hasOwnProperty('router')) {
                    let params = {};
                    if (item.hasOwnProperty('params')) {
                        Object.assign(params, item.params);
                    }
                    if (item.hasOwnProperty('paramsData')) {
                        Object.assign(params, item.paramsData);
                    }
                    this.$router.push({name: item.router, params: params});
                } else if (item && typeof item.func === "function") {
                    if (item.hasOwnProperty('funcData')) {
                        item.func(item.funcData);
                    } else {
                        item.func();
                    }
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    .distributorMain{
        background-color: #F2F2F2;
        .top{
            background-color: #fff;
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            padding: 10px 10px 0;
            .li{
                height: 85px;
                position: relative;
                width: 49%;
                border-radius: 5px;
                margin-bottom: 10px;
                .li-box{
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
					padding-left: 15px;
                    color: #fff;
                    .bbx-font{
                        font-size: 28px;
                        vertical-align: super;
                        margin-right: 5px;
                        height: 100%;
                        line-height: 85px;
                    }
                    .text-box{
                        display: inline-block;
                        .text{
                            font-size: 14px;
                        }
                        .price{
                            font-size: 14px;
                        }
                    }
                }
                &:nth-child(1){
                    background-color: #FF6666;
                }
                &:nth-child(2){
                    background-color: #00CCFF;
                }
                &:nth-child(3){
                    background-color: #3399FF;
                }
                &:nth-child(4){
                    background-color: #00CC66;
                }
            }
        }
        .menu{
            margin-top: 10px;
            background-color: #fff;
            .menu-item{
                height: 55px;
                line-height: 55px;
                border-bottom: 1px solid #eee;
                padding: 0 20px;
                .bbx-font{
                    font-size: 22px;
                }
                .text{
                    vertical-align: super;
                    padding-left: 15px;
                }
                .jiantou{
                    float: right;
                    font-size: 16px;
                    color: #8A8A8A;
                    &:active{
                        opacity: 0;
                    }
                }
            }
        }
    }
</style>